import React, { Component } from "react";
// import PowerEDKids from "./PowerEd subcomponents/PowerEDKids";
// import PowerEDSenior from "./PowerEd subcomponents/PowerEDSenior";
// import PowerEDWomen from "./PowerEd subcomponents/PowerEDWomen";
// import PowerEDYouth from "./PowerEd subcomponents/PowerEDYouth";
import PowerEdYouth from "../../../assets/images/powered-youth.png";
import PowerEdSenior from "../../../assets/images/powered-senior.png";
import PowerEdChildren from "../../../assets/images/powered-children.png";
import PowerEdWomen from "../../../assets/images/powered-women.png";
import PowerED from "../../../assets/images/powered.png";
import Awareness from "../../../assets/images/aware.png";
import { NavLink } from "react-router-dom";

export default class PowerEd extends Component {
  render() {
    return (
      <div className="powered">
        <div className="powered__intro">
          <img
            className="powered__intro-logo"
            src={PowerED}
            alt="PowerED Logo"
          ></img>
          <h2 className="powered-title"></h2>
        </div>
        <div className="powered__container">
          {/* <img
            className="powered__container-aware"
            src={Awareness}
            alt="Powered By Awareness"
          ></img> */}

          <div className="powered__card kids">
            <div className="powered__card__imgcontainer">
              <img
                className="powered__card__imgcontainer-img"
                src={PowerEdChildren}
                alt="PowerED Children"
              ></img>
            </div>
            <div className="powered__card__info">
              <ul className="powered__card__info__list">
                <li className="powered__card__info__list-text">Ages xx-xx</li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
              </ul>
              <NavLink className="powered__card-button" to="/">
                Sign Up
              </NavLink>
            </div>
          </div>

          <div className="powered__card youth">
            <div className="powered__card__imgcontainer">
              <img
                className="powered__card__imgcontainer-img"
                src={PowerEdYouth}
                alt="PowerED Youth Logo"
              ></img>
            </div>
            <div className="powered__card__info">
              <ul className="powered__card__list">
                <li className="powered__card__info__list-text">Ages xx-xx</li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
              </ul>
              <NavLink className="powered__card-button" to="/">
                Sign Up
              </NavLink>
            </div>
          </div>

          <div className="powered__card women">
            <div className="powered__card__imgcontainer">
              <img
                className="powered__card__imgcontainer-img"
                src={PowerEdWomen}
                alt="PowerED Women Logo"
              ></img>
            </div>
            <div className="powered__card__info">
              <ul className="powered__card__info__list">
                <li className="powered__card__info__list-text">Ages xx-xx</li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
              </ul>
              <NavLink className="powered__card-button" to="/">
                Sign Up
              </NavLink>
            </div>
          </div>

          <div className="powered__card senior">
            <div className="powered__card__imgcontainer">
              <img
                className="powered__card__imgcontainer-img"
                src={PowerEdSenior}
                alt="PowerED Senior Logo"
              ></img>
            </div>
            <div className="powered__card__info">
              <ul className="powered__card__info__list">
                <li className="powered__card__info__list-text">Ages xx-xx</li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
                <li className="powered__card__info__list-text">
                  placeholder info
                </li>
              </ul>
              <NavLink className="powered__card-button" to="/">
                Sign Up
              </NavLink>
            </div>
          </div>

          {/* <PowerEDKids />
          <PowerEDYouth />
          <PowerEDWomen />
          <PowerEDSenior /> */}
        </div>
      </div>
    );
  }
}
