import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Facebook from "../assets/icons/facebook.png";
import Email from "../assets/icons/email.png";
import Logo from "../assets/images/fit-logo-tag.png";

export default class Header extends Component {
  render() {
    return (
      <header className="header">
        <img className="header-logo" src={Logo} />
        <nav className="header__nav">
          <NavLink to="/" className="header__nav-link header__home">
            Home
          </NavLink>

          <NavLink to="/" className="header__nav-link">
            Programs
            <div className="header__dropdown">
              <NavLink
                to="/programs/powered"
                className="header__dropdown-text dropdown-powered"
              >
                PowerED
              </NavLink>
              <NavLink to="/" className="header__dropdown-text dropdown-anti">
                Anti-Bullying
              </NavLink>
              <NavLink to="/" className="header__dropdown-text dropdown-anti">
                Corporate
              </NavLink>
              <NavLink
                to="/calendar"
                className="header__dropdown-text dropdown-anti"
              >
                Calendar
              </NavLink>
            </div>
          </NavLink>

          <NavLink to="/" className="header__nav-link">
            About
            <div className="header__dropdown">
              <NavLink
                to="/"
                className="header__dropdown-text dropdown-gallery"
              >
                Gallery
              </NavLink>
              <NavLink
                to="/"
                className="header__dropdown-text dropdown-contact"
              >
                Contact Us
              </NavLink>
            </div>
          </NavLink>

          <NavLink to="/" className="header__nav-link">
            Instructors
          </NavLink>
        </nav>
      </header>
    );
  }
}
