import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "animate.css/animate.min.css";
import { NavLink } from "react-router-dom";

export default class Corporate extends Component {
  render() {
    return (
      <div className="corporate">
        <ScrollAnimation animateIn="fadeInDown" animateOnce="true">
          <h2 className="corporate-title">
            Workplace Violence Prevention Program
          </h2>
        </ScrollAnimation>
        <div className="corporate-flex">
          <ScrollAnimation animateIn="fadeInUp" animateOnce="true">
            <div className="corporate-container">
              <h3>
                Assists businesses to meet due diligence under laws governing
                violence prevention, harassment and safety in the workplace by
                training employees in conflict resolution, violence
                de-escalation and self defense. Provides risk assessments and up
                to date safety policies/procedures.
              </h3>
            </div>
          </ScrollAnimation>
          <div className="corporate__button-container">
            {/* <h2 className="homepagecontent__corporate__button-text">
                Bring safety to your workplace
              </h2> */}
            <NavLink
              className="corporate__button-button"
              to="/programs/corporate"
            >
              Learn More
            </NavLink>
          </div>
        </div>
      </div>
    );
  }
}
