import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "animate.css/animate.min.css";
import { NavLink } from "react-router-dom";
import PowerEdYouth from "../../../assets/images/powered-youth.png";
import PowerEdSenior from "../../../assets/images/powered-senior.png";
import PowerEdChildren from "../../../assets/images/powered-children.png";
import PowerEdWomen from "../../../assets/images/powered-women.png";
import Placeholder from "../../../assets/images/placeholder.jpg";
import Awareness from "../../../assets/images/aware.png";

export default class Intro extends Component {
  render() {
    return (
      <div className="intro">
        <img
          className="intro-awareness"
          src={Awareness}
          alt="Powered by Awareness Logo"
        ></img>
        <div className="intro__news">
          <div className="intro__news-new">
            <div className="intro__news-text">NEW</div>
          </div>
        </div>
        <div className="intro__flex">
          <ScrollAnimation
            animatePreScroll="false"
            class="intro__container"
            animateIn="fadeInLeft"
            animateOnce="true"
          >
            <div className="intro__container__card intro-card-one">
              <img
                src={PowerEdYouth}
                className="intro__container__card-logo"
                alt="img"
              ></img>

              {/* <NavLink to="/programs/powered">
                <button
                  to="/programs/powered"
                  className="intro__container__card-button"
                >
                  Learn More
                </button>
              </NavLink> */}
            </div>
          </ScrollAnimation>

          <ScrollAnimation
            animatePreScroll="false"
            class="intro__container"
            animateIn="fadeInRight"
            animateOnce="true"
          >
            <div className="intro__container__card intro-card-two">
              <img
                src={PowerEdSenior}
                className="intro__container__card-logo"
                alt="img"
              ></img>

              {/* <button to="/" className="intro__container__card-button">
                Learn More
              </button> */}
            </div>
          </ScrollAnimation>

          <ScrollAnimation
            animatePreScroll="false"
            class="intro__container"
            animateIn="fadeInLeft"
            animateOnce="true"
          >
            <div className="intro__container__card intro-card-three">
              <img
                src={PowerEdChildren}
                className="intro__container__card-logo"
                alt="img"
              ></img>

              {/* <button to="/" className="intro__container__card-button">
                Learn More
              </button> */}
            </div>
          </ScrollAnimation>

          <ScrollAnimation
            animatePreScroll="false"
            class="intro__container"
            animateIn="fadeInRight"
            animateOnce="true"
          >
            <div className="intro__container__card intro-card-four">
              <img
                src={PowerEdWomen}
                className="intro__container__card-logo"
                alt="img"
              />

              {/* <button to="/" className="intro__container__card-button">
                Learn More
              </button> */}
            </div>
          </ScrollAnimation>
        </div>
      </div>
    );
  }
}
